.postkit-blocks-color-wrapper {
	margin: 16px 0;

	.label {
		display: inline-block;
		font-size: 11px;
		font-weight: 500;
		line-height: 1.4;
		margin-bottom: 0;
		padding: 0px;
		text-transform: uppercase;
	}

	.color-header {
		align-items: center;
		display: flex;
		justify-content: space-between;
	}

	.color-indicator {
		background: none;
		border: none;
		cursor: pointer;
		height: 25px;
		position: relative;
		width: 25px;
	}

	.component-color-indicator {
		background-image: linear-gradient(45deg, #ddd 25%, transparent 0),
			linear-gradient(-45deg, #ddd 25%, transparent 0),
			linear-gradient(45deg, transparent 75%, #ddd 0),
			linear-gradient(-45deg, transparent 75%, #ddd 0);
		background-position: 0 0, 0 5px, 5px -5px, -5px 0;
		background-size: 10px 10px;
		height: 100%;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%;
	}
}

.postkit-blocks-clear-btn {
	background: #f00;
	border: none;
	border-radius: 3px;
	color: #fff;
	cursor: pointer;
	font-size: 11px;
	font-weight: 500;
	margin: 0 0 15px 15px;
	padding: 5px 15px;
	text-transform: uppercase;
}
